import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { Link, Route, Switch } from "react-router-dom";
import './App.scss';

function GroupUpload() {

    let state = useSelector((state) => state);
    let history = useHistory();
    let dispatch = useDispatch();

    return (
        // title, sentence, writer, introduction, field, category, theNum, deadline, address, reviewWriter, reviewSentence,
        <div className="group-upload">
            {/* <h4>그룹모집</h4> */}
            <form action="/group-upload" method="post">
                <input type="text" name='title' placeholder='제목' />
                <hr />
                <textarea name="sentence" id="" cols="30" rows="2" maxLength={20} placeholder='간단한 소개글을 작성해주세요!'></textarea>
                <hr />
                <select name="category" id="" required>
                    <option value="" disabled selected>카테고리 선택</option>
                    <option value="레슨">레슨</option>
                    <option value="홈/리빙">홈/리빙</option>
                    <option value="이벤트">이벤트</option>
                    <option value="비즈니스">비즈니스</option>
                    <option value="디자인/개발">디자인/개발</option>
                    <option value="건강/미용">건강/미용</option>
                    <option value="알바">알바</option>
                    <option value="기타">기타</option>
                </select>
                <hr />
                <select name="theNum" id="" required>
                    <option value="" disabled selected>모집 인원수 선택</option>
                    <option value="1~5명">1~5명</option>
                    <option value="5~10명">5~10명</option>
                    <option value="10~15명">10~15명</option>
                    <option value="15명 이상">15명 이상</option>
                </select>
                <hr />
                <label htmlFor="deadline"><strong>모집 기한</strong></label><input name='deadline' type="date" className='deadline' id='deadline' required />
                <hr />
                <div className="btn">
                    <button type='submit'>글 작성하기</button>
                </div>
            </form>
        </div>
    )
}

export default GroupUpload;

