import { useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import './App.scss';
import { Accordion } from "react-bootstrap";

function MentoDetail0() {

    let state = useSelector((state) => state);
    let { id } = useParams();

    function call(){
        alert('멘토에게 요청을 보냈습니다.');
    }

    return (
        <div className="mento-detail">
            <div className="profile">
                <div className="img"><img src={state.reducer2[0].mentoList0[id].img} alt="" /></div>
                <div className="card">
                    <div className="namediv">
                        <span className="name">{state.reducer2[0].mentoList0[id].name} 멘토</span>
                        <span className="feature badge bg-secondary">{state.reducer2[0].mentoList0[id].feature}</span>
                    </div>
                    <hr />
                    <p className="ment">{state.reducer2[0].mentoList0[id].ment}</p>
                    <div className="number">
                        <span>스터디 인원</span>
                        <span className="num">{state.reducer2[0].mentoList0[id].num} 명</span>
                    </div>
                    <hr />
                    <span className="message">멘토에게 메세지를 보내보세요</span>
                    <div className="button">
                        <button className="send btn-dark btn-lg">메세지 보내기</button>
                        <button onClick={call} className="call btn-danger btn-lg">멘토 요청하기</button>
                    </div>
                </div>
            </div>
            <div className="container">
                <span className="title">멘토에게 알려주세요</span>
                <div className="box">
                    <div className="percent">
                        <span>완료 시 멘토가 당신을 알 수 있어요</span>
                        <div className="bar"><div className="fill">64%</div></div>
                    </div>
                    <span className="question">멘토를 찾는 이유는 무엇인가요?</span>
                    <div className="reason">
                        <ul>
                            <li><input type="radio" id='reason1' name="reason" /><label htmlFor="reason1">같이 축구할 친구를 찾고 싶어요</label></li>
                            <li><input type="radio" id='reason2' name="reason" /><label htmlFor="reason2">여자친구가 축구를 좋아해요</label></li>
                            <li><input type="radio" id='reason3' name="reason" /><label htmlFor="reason3">운동하고 싶어요</label></li>
                            <li><input type="radio" id='reason4' name="reason" /><label htmlFor="reason4">전문가에게 배우고 싶어요</label></li>
                            <li><input type="radio" id='reason5' name="reason" /><label htmlFor="reason5">새로운 취미를 배우고 싶어요</label></li>
                        </ul>
                        <button onClick={call}>완료</button>
                    </div>
                </div>
            </div>
            <div class="container2">
                <h2 class="text-center">자주 묻는 질문</h2>
                <Accordion className="faq">
                    <Accordion.Item eventKey="0">
                        <Accordion.Header>Q. '멘토'는 어떤 서비스인가요?</Accordion.Header>
                        <Accordion.Body>
                            '멘토'는 다양한 분야의 멘토를 찾을 수 있고 멘토와 멘티가 같이 커뮤니티를 형성 할 수 있는 웹사이트입니다.
                        </Accordion.Body>
                    </Accordion.Item>
                    <Accordion.Item eventKey="1">
                        <Accordion.Header>Q. 멘토/멘티와 어떻게 거래하나요?</Accordion.Header>
                        <Accordion.Body>
                            멘티가 원하는 서비스의 요청을 보내면, 멘토는 멘티에게 연락을 보냅니다. <br />
                            멘티는 최대 48시간 이내에 멘토의 연락을 받아볼 수 있습니다.
                        </Accordion.Body>
                    </Accordion.Item>
                    <Accordion.Item eventKey="2">
                        <Accordion.Header>Q. '멘토'를 통해 멘토/멘티와 연결되면 수수료가 있나요?</Accordion.Header>
                        <Accordion.Body>
                            '멘토'를 통해 멘토/멘티와 연결하더라도 수수료가 부과되지 않습니다.
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
            </div>
        </div>
    )
}

export default MentoDetail0;